.main-wrapper {

    // .main-wrapper__section

    &__section {
    }
}
.info {
    
    // .info__container

    &__container {
        margin-top: 100px;
        @include sm{
           margin-top: 50px;
          }
    }

    // .info__inner

    &__inner {
    }

    // .info__wrapper

    &__wrapper {
    }

    // .info__main

    &__main {
    }

    // .info__main-text

    &__main-text {
        color: var(--light-color);
        font-size: 58px;
        margin-bottom: 30px;
        @include xxl{
            font-size: 35px;
        }
        @include lg{
            font-size: 30px;
        }
        @include sm{
            font-size: 20px;
           }
       
    }

    // .info__descr

    &__descr {
        display: flex;
        gap: 100px;
        align-items: center;
        @include xxl{
       align-items: start;
        }
        @include sm{
            display: block;
            gap: 0px;
           }
    }

    // .info__descr-text

    &__descr-text {
        color: var(--secondary-color);
        font-size: 20px;
        width: 500px;
        @include lg{
            font-size: 16px;
            width: 412px;
        }
    }

    // .info__text

    &__text {
        width: 500px;
        @include lg{
            width: 350px;
        }
    }

    // .info__descr-advant

    &__descr-advant {
        display: grid;
        grid-template-columns: repeat(12, 3fr) ;
        width: 800px;
        @include sm{
            width: 420px;
           }
    }

    // .info__advant-item

    &__advant-item {
        grid-column: 6 span;
        display: flex;
        gap:15px;
        justify-content: center;
        align-items: center;
        margin: 35px 220px;
        @include xxl{
            grid-column: 12 span;
            margin: 5px;
           
        }
    }

    // .info__item-linia

    &__item-linia {
        
    }

    // .info__linia

    &__linia {
        width: 3px;
        background: linear-gradient(#266eff31, #2670FF);
        border: none;
        height: 75px;
        border-radius: 10px;
        @include xxl{
            height: 55px;
        }
    }

    // .info__item-text

    &__item-text {
        display: flex;
        flex-direction: column;
        gap: 15px;
    }

    // .info__text-main

    &__text-main {
        color: var(--blue-color);
        font-weight: bold;
        font-size: 28px;
        font-family: 'Roboto';
        margin: 0px;
        width: 400px;
        @include xxl{
            font-size: 22px;
        }
        @include lg{
            font-size: 20px;
        }
    }

    // .info__text-descr

    &__text-descr {
        color: var(--secondary-color);
        font-size: 16px;
        font-family: 'Roboto';
        margin: 0px;
        font-size: 14px;
        @include lg{
            font-size: 14px;
        }
    }
}
