    // .news-sytes__main

    &__main {
    }

    // .news-sytes__main-text

    &__main-text {
        color: var(--light-color);
        font-size: 50px;
        font-weight: 700;
        padding-top: 120px;
        display: flex;
        justify-content: center;
        align-items: center;
        @include lg{
            font-size: 25px;
            font-weight: 600;
        }
    }

    // .news-sytes__content

    &__content {
    }

    // .news-sytes__content-news

    &__content-news {
        display: block;
        
    }

    // .news-sytes__news

    &__news {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 30px;
        margin-bottom: 30px;
        @include sm{
            display: block;
        }
    }

    // .news-sytes__news-img

    &__news-img {
    }

    // .news-sytes__img

    &__img {
        border-radius: 10px;
        height: 270px;
  width: 400px;

    }

    // .news-sytes__news-descr

    &__news-descr {
    }

    // .news-sytes__descr-text

    &__descr-text {
        display: block;
        gap: 30px;
width: 630px;
@include lg{
    width: 360px;
}
    }

    // .news-sytes__text
    &__text-main{
        color: var(--light-color);
        font-size: 28px;
        margin-bottom: 40px;
        text-align: center;
        @include lg{
            font-size: 22px;
        }
        @include sm{
            font-size: 5vw;
        }
    }
    &__text {
        color: var(--secondary-color);
        font-size: 20px;
        @include lg{
            font-size: 18px;
        }
        @include sm{
            font-size: 5vw;
        }
    }
    #reverse{
        @include sm{
            display: flex;
            flex-direction: column-reverse;
        }
       
    }
}
