.application {

    // .application__footer

    &__footer {
        background-color: #252525;
    }
}
.footer {

    // .footer__container

    &__container {
        margin-top: 150px;
        @include sm{
            margin-top: 50px;
        }
    }

    // .footer__inner

    &__inner {
    }

    // .footer__wrapper

    &__wrapper {
    }

    // .footer__main

    &__main {
    }

    // .footer__main-log0

    &__main-logo {
        
        padding-top: 30px;
        display: flex;
        align-items: center;
        gap: 265px; 
        @include lg{
            gap: 100px;
            align-items: center;
        }
        @include sm{
           gap: 50px;
        }
    }

    // .footer__logo

    &__logo {
        display: flex;
        justify-content: start;
        width: 210px;
       min-width: 210px;
       @include lg{
        width: 144px;
        min-width: 144px;
        height: 55px;
       }
       @include sm{
        min-width: 100px;
        width: 100px;
        height: 40px;
    }
    }

    // .footer__logo-text

    &__logo-text {
        color: var(--light-color);
        font-size: 32px;
        @include lg{
            font-size: 28px;
        }
        @include sm{
            font-size: 14px;
        }
    }

    // .footer__main-contact

    &__main-contact {
        display: flex;
        align-items: center;
        gap: 340px;
        @include xxl{
            gap: 110px;
        }
        @include lg{
            gap: 60px;
        }
        @include sm{
            
            display: block;
        }
    
    }

    // .footer__contact-number

    &__contact-number {
    }

    // .footer__number

    &__number {
        
    }

    // .footer__number-text

    &__number-text {
        display: flex;
        align-items: center;
        gap: 20px;
        margin: 0px;
        margin-left: 0px;
    }

    // .footer__text
    &__text-num {
        color: var(--blue-color);
        font-size: 34px;
        margin: 25px;
        margin-left: 0px;
        width: 300px;
        @include xxl{
            font-size: 24px;
            width: 230px;
            margin-right: 0px;
        }
        @include lg{
            font-size: 20px;
            width: 176px;
        }
    }

    &__text {
        font-size: 34px;
        color: var(--secondary-color);
        margin: 0px;
        margin-left: 0px;
        @include xxl{
            font-size: 24px;
        }
        @include lg{
            font-size: 20px;
        }
    }

    // .footer__number-icon

    &__number-icon {
        display: flex;
        align-items: center;
        gap: 20px;
    }

    // .footer__icon

    &__icon {
    }

    // .footer__icon-img

    &__icon-img {
    }

    // .footer__contact-map

    &__contact-map {
        margin: 20px;
        border-radius: 10px;
        @include sm{
            margin: 0px;
        }
    }
    
}
