.main-wrapper {

    // .main-wrapper__section

    &__section {
    }
}
.order {

    // .order__container

    &__container {
        margin-top: 100px;
        @include sm{
            margin-top: 50px;
        }
    }

    // .order__inner

    &__inner {
    }

    // .order__wrapper

    &__wrapper {
    }

    // .order__main

    &__main {
    }

    // .order__main-text

    &__main-text {
        color: var(--light-color);
        font-size: 58px;
        margin-bottom: 30px;
        display: flex;
        justify-content: center;
        @include xxl{
            font-size: 35px;
        }
        @include lg{
            font-size: 30px;
        }
        @include sm{
            font-size: 20px;
        }
    }

    // .order__order-content

    &__order-content {
        display: flex;
        gap: 60px;
    }

    // .order__content-img

    &__content-img {
    }

    // .order__img

    &__img {
        max-width: 694px;
        max-height: 627px;
        width: 694px;
        height: 580px;
        border-radius: 10px;
        @include xxl{
            width: 427px;
            height: 490px;
            
        }
        @include sm{
            display: none;
        }
    }

    // .order__content-form

    &__content-form {
    }

    // .order__form

    &__form {
        display: flex;
        flex-direction: column;
        @include sm{
            margin-left: -13vw;
        }
    }

    // .order__form-text

    &__form-text {
        color: var(--light-color);
        font-size: 22px;
        font-weight: 500;
        margin: 10px;
        margin-left: 0px;
        margin-top: 0px;
        @include xxl{
            font-size: 18px;
        }
    }

    // .order__form-input

    &__form-input {
        max-width: 550px;
        max-height: 50px;
        width: 550px;
        height: 50px;
        border: none;
        background-color: #252525;
        border-radius: 5px;
        margin-bottom: 20px;

        color: var(--secondary-color);
        font-size: 20px;
        padding: 10px 30px;
        font-family: 'Roboto';
        @include xxl{
            width: 450px;
            height: 40px;
            margin-bottom: 15px;
        }
        @include lg{
            width: 245px;
            height: 40px;
        }
        @include sm{
            width: 89vw;
        }
    }

    // .order__input-select

    &__input-select {
       
    }

    // .order__form-button

    &__form-button {
    }

    // .order__button

    &__button {
        display: flex;
        align-items: center;
        justify-content: center;
        width:  270px;
        height: 55px;
        border: 2px solid var(--acccent-color);
        border-radius: 5px;
        background-color: transparent;
        color: var(--acccent-color);
        font-size: 24px;
        cursor: pointer;
        &:hover{
            background-color: var(--acccent-color);
            color: var(--black-color);
            transition: 0.7s;

        }
        @include lg{
           width: 232px;
           height: 40px;
           font-size: 18px;
        }
    }
}
.container {
}
.order {

    // .order__fotm-arend

    &__fotm-arend {
        display: flex;
        gap: 10px; 
        @include lg{
            display: block;
            gap: 10px;
        }
        
        @include xxl{
            display: flex;
        gap: 10px; 
        }
        @include sm{
            display: flex;
        gap: 10px; 
        }
    }

    // .order__arend-input

    &__arend-input {
        
        display: flex;
        flex-direction: column;
    }

    // .order__form-text

    &__form-text {
    }

    // .order__input

    &__input {
        max-width: 270px;
        max-height: 50px;
        width: 270px;
        height: 50px;
        border: none;
        background-color: #252525;
        border-radius: 5px;
        margin-bottom: 20px;

        color: var(--secondary-color);
        font-size: 20px;
        padding: 10px 30px;
        font-family: 'Roboto';
        @include xxl{
            width: 220px;
            height: 40px;
            margin-bottom: 15px;
        }
        @include lg{
            width: 245px;
            height: 40px;
        }
        @include sm{
            width: 43vw;
        }
    }
}
