.header-news{
    z-index: 2;
    position: fixed;
    top: 0;
    scroll-behavior: smooth;
    padding: 13px 0px;
    width: 100%;
    background-color: var(--black-color);
    z-index: 1000;
    &__inner {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 50px;
        
    }
    
}

