.news {
 
	// .news__container

	&__container {
		margin-top: 100px;
		@include sm{
            margin-top: 50px;
        }
	}

	// .news__inner

	&__inner {
	}

	// .news__wrapper

	&__wrapper {
	}

	// .news__main

	&__main {
	}

	// .news__main-text

	&__main-text {
		color: var(--light-color);
        font-size: 58px;
        margin-bottom: 30px;
		display: flex;
        justify-content: center;
		@include xxl{
			font-size: 35px;
		}
		@include lg{
            font-size: 30px;
        }
	}

	// .news__content

	&__content {
		display: grid;
        grid-template-columns: repeat(12, 1fr);
		
		margin-top: 100px;
		@include lg{
			display: flex;
			justify-content: center;
			flex-direction: column;
		}
	}

	// .news__card

	&__card {
		grid-column: 4 span;

		width: 465px;
		height: 500px;
		margin: 0px;
		border-radius: 10px;
		background-color: transparent;
		transition: .5s ease-in-out;
		@include xxl{
		grid-column: 6 span;
		}
		@include lg{
            grid-column: 12 span;
			display: flex;
			justify-content: center;
			margin-left: 18vw;
        }
		@include sm{
            margin-left: -12vw;
        }

		&:hover {
			.news__card-caption {
				top: 50%;
				transform: translateY(-50%);
			}

			.news__card-image {
				transform: translateY(-10px);
			}

			.news__card-thumb {
				&::after {
					top: 0;
				}
			}

			.card__snippet {
				margin: 20px 0;
			}
		}
	}

	// .news__card-thumb

	&__card-thumb {
		cursor: pointer;
		border-radius: 10px;
		position: relative;
		max-height: 400px;
		overflow: hidden;
		
		@include breakpoint(desktop) {
			max-height: 500px;
		}

		&::after {
			position: absolute;
			top: 0;
			display: block;
			content: '';
			width: 100%;
			height: 100%;
			background: linear-gradient(0deg, rgba(0, 0, 0, .5) 40%, rgba(255, 255 ,255 , 0) 100%);
			transition: .3s;
			
			@include breakpoint(desktop) {
				top: calc(100% - 140px);
			}
		}
	}

	// .news__card-image

	&__card-image {
		transition: .5s ease-in-out;
        
        border-radius: 10px;
		margin: 0px;
	}

	// .news__card-caption

	&__card-caption {
		position: absolute;
		top: 50%;
		z-index: 1;
		padding: 0 20px;
		color: white;
		transform: translateY(-50%);
		text-align: center;
		transition: .3s;

		@include breakpoint(desktop) {
			top: calc(100% - 110px);
			transform: unset;
		}
	}

	// .news__card-title

	&__card-title {
		max-height: 85px;
    overflow: hidden;
		font-family: 'Playfair Display', serif;
		font-size: 22px;
		line-height: 28px;
		font-weight: 500;
		font-family: 'Roboto';
		text-overflow: ellipsis;
    -webkit-line-clamp: 3;
		-webkit-box-orient: vertical;
	}

	// .news__card-snippet

	&__card-snippet {
		max-height: 150px;
		margin: 20px 0;
		overflow: hidden;
		font-family: 'Roboto';
		font-size: 18px;
		line-height: 20px;
		text-overflow: ellipsis;
		transition: .5s ease-in-out;
    	-webkit-line-clamp: 5;
		-webkit-box-orient: vertical;
		
		@include breakpoint(desktop) {
			margin: 60px 0;
		}
	}
}
