    &__section {
    }
}
.slider {
    height: 585px;
    object-fit: cover;
    background-repeat: no-repeat;
    background-size: cover;
  background-position: center;
    // .slider__container
    font-family:  'Kodchasan';
    @include xxl{
       
    }
    @include lg{
        height: 360px;
    }
    @include sm{
       width: 400px;
       height: 250px;
    }
    
    &__container {
    }

    // .slider__inner

    &__inner {
    }

    // .slider__wrapper

    &__wrapper {
    }

    // .slider__slide

    &__slide {
        padding-top: 110px ;
        @include sm{
            padding-top: 112px;
          }
    }

    // .slider__slide-info

    &__slide-info {
    }

    // .slider__slide-title

    &__slide-title {
        margin: 0px;
    }
    #logo-text{
        color: var(--blue-color);
        font-weight: normal;
        font-size: 64px;
     @include lg{
        font-size: 40px;
     } 
     @include sm{
        font-size: 20px;
     }  
    }
    #main-text{
        font-family:'Roboto';
        color: var(--light-color);
        font-weight: normal;
        font-size: 46px;
        @include lg{
            font-size: 35px;
         }  
         @include sm{
            font-size: 15px;
         }  
    }

    // .slider__slide-link

    &__slide-text {
        color: var(--light-color);
        font-size: 24px;
        font-family:'Roboto';
        width: 450px;
        margin-top: 40px;
        @include lg{
            font-size: 20px;
         }  
         @include sm{
            font-size: 10px;
            margin-top: 10px;
         }  
    }

    // .slider__slide-button

    &__slide-button {
        font-family: 'Inter';
        display: flex;
        gap: 30px;
        margin-top: 80px;
        @include lg{
            margin-top: 10px;
         }  
    }

    // .slider__button

    &__button {
        width: 205px;
        height: 45px;
        border: 2px solid #FF7A00;
        border-radius: 5px;
        backdrop-filter: blur(4px);
        -webkit-backdrop-filter: blur(4px);
        -moz-backdrop-filter: blur(4px);
        background-color: transparent;
        color: var(--acccent-color);
         font-family: 'Roboto'  !important;
        font-size: 24px;
        font-weight: normal;
        cursor: pointer;
        &:hover{
            background-color: var(--acccent-color);
            color: var(--black-color);
            transition: 0.7s;

        }
        @include lg{
            width: 165px;
            height: 35px;
            font-size: 20px;
         }  
         @include sm{
            width: 70px;
            height: 20px;
            font-size: 8px;
            border: 0.5px solid #FF7A00;
        border-radius: 2px;
         }  
    }
    #autoparck{
        &:hover{
            background-color: var(--acccent-color);
            color: var(--black-color);
            transition: 0.7s;
            transform: scale(1.1, 1.1);
    
        }
        
        background-color: var(--acccent-color);
        color: var(--black-color);
    }
    

    // .slider__navigation

    &__navigation {
    }
}
