.main-wrapper {

    // .main-wrapper__section

    &__section {
    }
}
.cards {
    

    // .cards__container

    &__container {
        margin-top: 100px;
        @include sm{
            margin-top: 50px;
           }
    }

    // .cards__inner

    &__inner {
    }

    // .cards__title

    &__title {
        color: var(--light-color);
        font-size: 58px;
        margin-bottom: 30px;
        display: flex;
        justify-content: center;
        @include xxl{
            font-size: 35px;
        }
        @include lg{
            font-size: 20px;
        }
        @include sm{
            margin-bottom: 0px;
        }
    }

    // .cards__title-tabs

    &__title-tabs {
        display: flex;
        gap: 60px;
        margin-top: 100px;
        @include xxl{
            gap: 20px;
        }
    }

    // .cards__tabs-content

    &__tabs-content {
    }

    // .cards__content-img

    &__content-img {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    // .cards__img-circle

    &__img-circle {
        position: absolute;
        width: 9%;
         margin-top: -10px;
        margin-left: 5px;

        max-width: 158px;
  max-height: 158px;
  min-width: 158px;
  min-height: 158px;
  @include lg{
    max-width: 112px;
    max-height: 112px;
    min-width: 10vw;
    min-height: 10vw;
}
    }
    #one{

    }
    #two{
        opacity: 0;
    }
    #fre{
        opacity: 0;
    }
    #fo{
        opacity: 0;
    }


    // .cards__img
   
		// .cards__card-img

		&__card-img {
            
            object-fit: cover;
           
		}


    &__img {
        position: relative;
        cursor: pointer;
       height: 90px;
       @include xxl{
        width: 25vw;
        height: 10vw;
        object-fit: contain;
        
    }
    }

    // .cards__content-text

    &__content-text {
        display: flex;
        justify-content: center;
        color: var(--secondary-color);
        font-size: 26px;
        margin: 10px;
        gap: 340px;
        @include xxl{
            font-size: 2vw;
            gap: 20vw;
        }
        @include lg{
            font-size: 2vw;
            gap: 2vw;
        }
        @include sm{
            font-size: 4vw;
        }
    }
    &__content-tabs{
        color: var(--secondary-color);
        font-size: 26px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 40px;
        @include lg{
            font-size: 16px;
        }
        @include sm{
           font-size: 14px;
           }
    }
    #accent{
        color: var(--acccent-color);
    }

    // .cards__filter

    &__filter {
display: flex;
align-items: center;
color: var(--secondary-color);
gap:  30px;
font-size: 0px;
    }

    // .cards__filter-price

    &__filter-price {
        margin: 15px;
    }

    // .cards__filter-moch

    &__filter-moch {
        margin: 15px;
    }

    // .cards__list

    &__list {
        display: grid;
        grid-template-columns: repeat(12, 3fr) ;
        gap: 20px;
        padding: 0px;
        
    }

    // .cards__item

    &__item {
    grid-column: 6 span;
    @include sm{
     grid-column: 12 span;       
    }
    }

    // .cards__card

    &__card {
        background-color: #252525;
        border-radius: 10px;
    }

    // .cards__card-main

    &__card-main {
    }

    // .cards__main-text

    &__main-text {
        display: flex;
        justify-content: center;
        color: var(--blue-color);
        font-weight: 500;
        border-bottom: 2px solid #000000;
        margin-left: 5px;
        width: 618px;
        height: 50px;
        margin: 15px;
        font-family: 'Arial';
        font-size: 32px;
        @include xxl{
            width: 467px;
        }
        @include lg{
            width: 43vw;
            font-size: 4vw;
        }
        @include sm{
            width: 86vw;
            font-size: 7vw;
        }
    }

    // .cards__card-content

    &__card-content {
    }

    // .cards__text-name

    &__text-name {
        font-weight: 500;
        width: 130px;
        display: flex;
       justify-content: start;
       margin: 10px;
    }

    // .cards__text-info

    &__text-info {
        font-weight: 700;
        color: var(--light-color);
       width: 170px;
       display: flex;
       justify-content: end;
       margin: 10px;
    }

    // .cards__card-button

    &__card-button {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 30px;
    }

    // .cards__button

    &__button {
        display: flex;
        justify-content: center;
        align-items: center;
        color: var(--acccent-color);
        background-color: transparent;
        border: 2px solid var(--acccent-color);
        width: 300px;
        height: 55px;
        border-radius: 5px;
        color: var(--acccent-color);
        font-family: 'Roboto' !important;
        font-size: 24px;
        margin-bottom: 20px;
        text-align: center !important;
        
        cursor: pointer;
        &:hover{
            background-color: var(--acccent-color);
            color: var(--black-color);
            transition: 0.7s;
        }
    }
    #bis1{
        display: block;
    }
    #bis2{
        display: block;
    }
    #bis3{
        display: block;
    }
    #bis4{
        display: block;
    }
    #prem1{
display: none;
    }
    #prem2{
        display: none;
            }
            #prem3{
                display: none;
                    }
                    #prem4{
                        display: none;
                            }
    #vnedorog{
display: none;
    }
    #vnedorog1{
        display: none;
            }
    #sport{
display: none;
    }
    #sport1{
        display: none;
            }

}
