    }

    // .header__row--top

    &__row--top {
    }

    // .header__navbar

    &__navbar {
    }
    
}
.navbar {

    // .navbar__menu

    &__menu {
        display: flex;
        
    }

    // .navbar__item

    &__item {
        
        margin: 0px 10px;
        
        list-style: none;
    }

    // .navbar__link

    &__link {
        color: var(--light-color);
        font-size: 18px ;
        
    }
}

.logo {

    &__brand{
       display: flex;
       justify-content: center;
      
       min-width: 170px;
       min-height: 60px;
       @include xxl{
           margin-right: 55vw;
       }
       @include lg{
        margin-right: 45vw;
    }
    @include sm{
      margin-right: -30vw;
      min-width: 104px;
      min-height: 40px;
    }
    }
}




@media (max-width:1920px){
    #menu__toggle {
        opacity: 0;
        display: none;
      }
}

@media (max-width:1024px){
    #menu__toggle {
        opacity: 0;
        height: 3px;
      }
      #menu__toggle:checked + .menu__btn > span {
        transform: rotate(45deg);
      }
      #menu__toggle:checked + .menu__btn > span::before {
        top: 0;
        transform: rotate(0deg);
      }
      #menu__toggle:checked + .menu__btn > span::after {
        top: 0;
        transform: rotate(90deg);
      }
      #menu__toggle:checked ~ .menu__box {
        left: 0 !important;
      }
      .menu__btn {
        display: block;
        position: fixed;
       
        top: 45px;
        left: 900px;
        width: 26px;
        height: 26px;
        cursor: pointer;
        z-index: 111111;
        @include lg{
          left: 85vw;
       }  
       @include sm{
        top: 30px;
      }
      }
      .menu__btn > span,
      .menu__btn > span::before,
      .menu__btn > span::after {
        display: block;
        position: absolute;
        border-radius: 5px;
        
        width: 100%;
        height: 3px;
        background-color: var(--blue-color);
        transition-duration: .25s;
        z-index: 111111;
        
      }
      .menu__btn > span::before {
        content: '';
        top: -8px;
      }
      .menu__btn > span::after {
        content: '';
        top: 8px;
      }
      .menu__box {
        display: flex;
        flex-direction: column;
        position: fixed;
        top: 0;
        left: -100%;
        width: 300px;
        height: 100%;
        margin: 0;
        padding: 80px 0;
        list-style: none;
        background-color: #252525;
        box-shadow: 2px 2px 6px rgba(0, 0, 0, .4);
        transition-duration: .25s;
        z-index: 112;
      }
      .menu__item {
       display: flex;
        justify-content: center;
        align-items: center;
        position: sticky;
        margin-top:  10px;
       
        font-family: 'Roboto', sans-serif;
        color:var(--light-color);
        font-weight: 500;
        font-size: 20px;
        text-decoration: none;
        transition-duration: .25s;
        
        z-index: 10;
        text-align: center;
      }

      .menu__item:hover {
        background-color: #CFD8DC;
      }
}
